/* ------------ Variables ------------ */
/** ---------- Media Queries Mixins ---------- **/
/** ---------- Media Queries Mixins ---------- **/
/* ---------Globales--------- */
/* ---------Globales--------- */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000B29;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/* ---------- Utilidades ---------- */
/* ---------- Utilidades ---------- */
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/** UI **/
.header {
  background-color: #000B29;
  padding: 0.6rem 0 0.6rem 0;
}

.barra {
  display: flex;
  flex-direction: column;
}
.barra img {
  max-width: 23.6rem;
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #F8F5F2;
  text-decoration: none;
  margin: 1rem 0 1rem 0;
  font-size: 1.4rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.43rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.site-footer .footer-up {
  background-color: #000000;
  padding: 2rem;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 768px) {
  .site-footer .footer-up {
    flex-direction: row;
  }
}
.site-footer .footer-up .footer-descarga h4 {
  color: #F8F5F2;
  margin: 1rem;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
}
.site-footer .footer-up .footer-descarga ul {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: center;
}
.site-footer .footer-up .footer-descarga ul li {
  padding: 0.2rem 0;
}
.site-footer .footer-up .footer-descarga ul li a {
  color: #808080;
  font-size: 1.4rem;
  font-weight: 500;
}
.site-footer .footer-up .footer-logo img {
  max-width: 100%;
  width: 100%;
  height: auto;
}
.site-footer .footer-up .footer-acerca h4 {
  color: #F8F5F2;
  margin: 1rem;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
}
.site-footer .footer-up .footer-acerca ul {
  list-style: none;
  padding: 0;
  margin: 0;
  text-align: center;
}
.site-footer .footer-up .footer-acerca ul li {
  padding: 0.2rem 0;
}
.site-footer .footer-up .footer-acerca ul li a {
  color: #808080;
  font-size: 1.4rem;
  font-weight: 500;
}
.site-footer .footer-downd {
  background-color: #000B29;
  padding: 0.1rem;
  margin: 0;
}
.site-footer .footer-downd p {
  color: #F8F5F2;
  font-size: 1.5rem;
  text-align: center;
}